function GetToolTipConnection(value) {
    return connectionTooltip[value];
}
function GetColor(position, connectionScore, defaultColor = '#FFFFFF') {
    const shouldBeColored = position <= connectionScore;
    if (!shouldBeColored) {
        return defaultColor;
    }
    if (connectionScore >= 4) {
        return "#37F28D";
    } else if (connectionScore >= 3) {
        return "#FAC919";
    } else if (connectionScore >= 1) {
        return "#ED4C5A";
    }
    return defaultColor;
};
