function LoadCodingQuestion() {
    var isExists = document.getElementById('InjectTest');
    var testingId = GlobalObj.TestId;
    var interviewId = GlobalObj.InterviewId;
    if (isExists != null) {
        var url = getInterviewUrl() + "/LoadCodeTestWindow?TestId=" + testingId + "&InterviewId=" + interviewId;
        //console.log(url);
        $.ajax({
            url: url,
            type: 'GET',
            dataType: 'html',
            data: {},
            crossDomain: true,
            cache: false,
            async: true,
            beforeSend: function () {
                $('.proctor-loader').css("visibility", "visible");
            },
            success: function (response) {
                var callBack = () => {
                    var result = response;
                    PushTracking(70);
                    $('#InjectTest').html(result);
                    InitiatingTimer();
                    hide(presenterController);
                    hide(startCodingTestButton);
                }
                callBack();
            },
            complete: function () {
                $('.proctor-loader').css("visibility", "hidden");
            },
            error: function (response) {
                ToastMessage(response.responseText, false);
            }
        });
        if (typeof (syncChatMessage) != 'undefined') {
            syncChatMessage();
        }
    }
}
function LoadQuestion(panelStatus, StartTimer=false) {
    panelStatus = panelStatus ?? "0";
    var isExists = document.getElementById('InjectTest');
    var testingId = GlobalObj.OfferingId;
    if (isExists == null) {
        $("#new-inject-test").html('<div id="InjectTest"></div>');
        isExists = document.getElementById('InjectTest');
    }
    if (isExists != null) {
        var url = getInterviewUrl() + "/LoadInterviewTest?Id=" + (testingId ?? params.OfferingId) + "&panelStatus=" + panelStatus;
        //console.log(url);
        $.ajax({
            url: url,
            type: 'GET',
            dataType: 'html',
            data: {},
            crossDomain: true,
            cache: false,
            async: true,
            beforeSend: function () {
                $('.proctor-loader').css("visibility", "visible");
            },
            success: function (response) {
                var callBack = () => {
                    var result = response;
                    PushTracking(70);
                    $('#InjectTest').html(result);
                    $("#new-inject-test").removeClass("hide");
                    $(".custom-step-8").addClass("hide");
                    if (panelStatus != "0" || StartTimer) {
                        InitiatingTimer();
                    }
                    hide(presenterController);
                    hide(startMcqTestButton);
                    hide(startCodingTestButton);
                    hide(startTestButton);
                }               
                callBack();
            },
            complete: function () {
                $('.proctor-loader').css("visibility", "hidden");
            },
            error: function (response) {
                ToastMessage(response.responseText, false);
            }
        });
        if (typeof (syncChatMessage) != 'undefined') {
            syncChatMessage();
        }
    }
}
