// Joining the room
async function JoinRoom(userName, roomCode) {
    // use room code to fetch auth token    
    var authTokenAPI = hmsActions.getAuthTokenByRoomCode({ roomCode }).then(async (authToken) => {
        // join room using username and auth token
        var joining = hmsActions.join({
            userName: userName,
            authToken: authToken,
            metaData: { userType: "candidate" },
            settings: {
                isAudioMuted: false,
                isVideoMuted: false,
                captureNetworkQualityInPreview: true
            }
        }).then(async (values) => {
            var slots = await UpdateSlots();
            change_tab("authentication");
            show(customView);
        });
        Events();
    }).catch(error => {
        ToastMessage("Failed to Join Room!!! Contact System Administrator", true);
    });
};

// Helper function to create html elements
function createElementWithClass(tag, className) {
    const newElement = document.createElement(tag);
    newElement.className = className;
    return newElement;
}
// Render a single peer
function renderPeer(peer) {
    var callback = null;
    const peerTileDiv = createElementWithClass("div", "peer-tile");
    const videoElement = createElementWithClass("video", "peer-video");
    const nonVideoElement = createElementWithClass("div", "peer-video-tile");
    const peerNetwork = createElementWithClass("div", "peer-network");
    const peerTileName = createElementWithClass("div", "peer-name");
    videoElement.autoplay = true;
    videoElement.muted = false;
    videoElement.controls = false;
    videoElement.playsinline = true;
    peerTileName.textContent = peer.name;
    hmsStore.subscribe((connectionQuality) => {
        if (connectionQuality) {
            peerNetwork.innerHTML = ConnectivitySVG(connectionQuality.downlinkQuality);
            if (IsSystemCheck) {
                if (connectionQuality.downlinkQuality >= 3.0) {
                    $('#speed-error').addClass('d-none');
                    $('#speed-success').removeClass('d-none');

                }
                else {
                    $('#speed-error').removeClass('d-none');
                    $('#speed-success').addClass('d-none');
                }
            }
        }
    }, selectConnectionQualityByPeerID(peer.id));
    var profileIcon = '<div data-testid="preview_avatar_tile" class="tile-short shape-circle shape-color">' + peer.name.substr(0, 2).toUpperCase() + '</div>';
    nonVideoElement.innerHTML = profileIcon;
    //track the camera Screen
    hmsStore.subscribe((track) => {
        if (!track) {
            return;
        }
        // console.log(track);
        PushTracking(track.enabled ? 73 : 520);//Camera Enabled or not
        if (track.enabled) {
            hmsActions.attachVideo(track.id, videoElement);
            videoElement.style.display = "";
            nonVideoElement.style.display = "none";
            callback = (def) => { CaptureUserPhoto(videoElement, def); };
        } else {
            hmsActions.detachVideo(track.id, videoElement);
            videoElement.style.display = "none";
            nonVideoElement.style.display = "";
            callback = (def) => { CaptureUserPhoto("undefined", def); };
        }
    }, selectVideoTrackByID(peer.videoTrack));
    peerTileDiv.append(videoElement);
    peerTileDiv.append(nonVideoElement);
    peerTileDiv.append(peerTileName);
    peerTileDiv.append(peerNetwork);
    renderedPeerIDs.add(peer.id);
    return { "elem": peerTileDiv, "deferred": callback };
}

// display a tile for each peer in the peer list
function renderPeers() {
    const peers = hmsStore.getState(selectPeers);
    peers.forEach((peer) => {
        if (!renderedPeerIDs.has(peer.id) && peer?.videoTrack && peer?.isLocal) {
            var render = renderPeer(peer);
            $(peersContainer).empty();
            peersContainer.append(render.elem);
            render.deferred && render.deferred(CameraCaptureCallBack);//trigger callback

            var photoMilliSeconds = PhotoCaptureTimer * 1000;

            var triggerTimeOut =
                setInterval(function () {
                    var stopRecordingForce = window.stopRecordingForce;
                    if (stopRecordingForce != null && stopRecordingForce.photo == false) {
                        render.deferred && render.deferred();
                    }
                    else if (stopRecordingForce != null && stopRecordingForce.photo == true) {
                        clearInterval(triggerTimeOut);
                        var callBack = (response) => {
                            var camVideo = document.getElementsByClassName("peer-video");
                            camVideo = camVideo.length > 0 ? camVideo[0] : camVideo;
                            camVideo.pause();
                            window.CompletedRecording.photo = true;
                        };
                        render.deferred && render.deferred(callBack);
                    }
                }, photoMilliSeconds);
        }
    });
}
function hide(el) {
    el.style.display = "none";
}
function show(el) {
    el.style.display = "";
}
function handleLeave(callBack = null) {
    var roomleave = function () {
        hmsActions.leave();
        change_tab('error-message');
        peersContainer.innerHTML = "";
        callBack && callBack();
        var url = '<br><a href="' + GlobalObj.PublicWebsite + '/User/Dashboard">Click Here for Dashboard</a>';
        $(".error-message").html(url);
        $('.proctor-loader').css("visibility", "hidden");
    }
    if (callBack == null) {
        roomleave();
    }
    else { callBack(roomleave); }
}
function handleEndRoom(callBack = null) {
    if (GlobalObj.CanRecord) {
        stopRecording().then((values) => {
            EndRoomClick("Test Room Ended Successfully by Candidate");
            ToastMessage("Test Room Ended Successfully by Candidate", true);
            callBack && callBack();
        });
    }
    else {
        EndRoomClick("Test Room Ended Successfully by Candidate");
        ToastMessage("Test Room Ended Successfully by Candidate", true);
        callBack && callBack();
    }
}
function EndRoomClick(msg) {
    try {
        const lock = true; // set to true to disallow rejoins
        const reason = msg;
        hmsActions.endRoom(lock, reason);
    } catch (error) {
        // Permission denied or not connected to room
        //console.error(error);
        ToastMessage(error, false);
    }
}
function fullScreenEnable() {
    if (!IsFullScreenCurrently(docElement))
        GoInFullscreen(docElement);
}

function exitHandler() {
    if (!docElement.webkitIsFullScreen && !docElement.mozFullScreen && !docElement.msFullscreenElement) {
        ToastMessage("Cant able to Cancel the FullScreen", true);
        setTimeout(() => { fullScreen.onclick(); }, 1000);
    }
}
