// Showing the required elements on connection/disconnection
function onConnectionVideo(isConnected) {
    if (isConnected) {
        show(conference);
        show(leaveBtn);
        show(presenterController);

    } else {
        hide(conference);
        hide(leaveBtn);
        hide(presenterController);
    }
}


