async function handleScreenShare() {
    $('#screen-share').val("SHARE YOUR SCREEN");
    const screenShareOn = !hmsStore.getState(selectIsSomeoneScreenSharing);
    await hmsActions.setScreenShareEnabled(screenShareOn);
    if (scopeData.IsProctorLive) {
        hide(startMcqTestButton);
        hide(startCodingTestButton);   
        hide(startTestButton);   
    }
    else {
        if (!scopeData.IsProctorLive && GlobalObj.IsMCQ) {
            startMcqTestButton.style.display = screenShareOn ? "" : "none";
        }
        if (!scopeData.IsProctorLive && GlobalObj.IsCoding) {
            startCodingTestButton.style.display = screenShareOn ? "" : "none";
        }
    }
    showScreenShareVideo();
    $('#screen-share').hide();
    $('.step-next-4').removeClass("hide");
}

async function showScreenShareVideo() {
    const screenShareOn = hmsStore.getState(selectIsSomeoneScreenSharing);
    if (screenShareOn) {
        const amIScreenSharing = hmsStore.getState(selectIsLocalScreenShared);
        const presenter = hmsStore.getState(selectPeerScreenSharing);
        const screenShareVideoTrack = hmsStore.getState(
            selectScreenShareByPeerID(presenter?.id)
        );

        if (amIScreenSharing) {
            fullScreenEnable();       
            if (scopeData.IsProctorLive) {
                hide(startMcqTestButton);
                hide(startCodingTestButton);
                hide(startTestButton);   
                const peers = hmsStore.getState(selectPeers);
                var panelMembers = peers.filter(x => x.roleName == "panel");
                screenShareStatus.textContent = (!panelMembers.length) ? "Please wait for Panel Member's to Join!!!" : "Please asks panel member's to Enable the Start Test via Chat Message!!!";                
            }
            else {
                screenShareStatus.textContent = "Preparing to start test!!!";
                if (!scopeData.IsProctorLive && GlobalObj.IsMCQ) {
                    startMcqTestButton.style.display = "";
                }
                if (!scopeData.IsProctorLive && GlobalObj.IsCoding) {
                    startCodingTestButton.style.display = "";
                }
            } 
            PushTracking(71);//start the sharing
        } else {
            hide(screenShareStatus);
            await hmsActions.attachVideo(screenShareVideoTrack?.id, screenShareVideo);
        }
    } else {
        PushTracking(72);//stopped the sharing
        screenShareStatus.textContent = "Preparing to start test!!!";
    }
}

function renderMessageList(messages) {
    if (messages.length === 0) {
        return;
    }
    let text = "";
    messages.forEach((msg) => {
        text += `\n${msg.senderName}: ${msg.message}`;
    });
    const elem = msgsElement;
    if (elem) {
        elem.textContent = text;
    }
}
function UpdateUnreadMessageCount(messages) {
    $(".msg-badge").html("");
    var ChatOpened = $("#chat").hasClass("show");
    if (messages > 0 && !ChatOpened) {
        $(".msg-badge").html(messages);
    }
}

function sendMessage() {
    if (!hmsStore.getState(selectIsConnectedToRoom)) {
        console.warn("can't send message when not conneted to room");
        return;
    }
    const msg = msgInputElement.value;
    if (msg && msg.trim() !== "") {
        hmsActions.sendBroadcastMessage(msg);
    } else {
        console.warn("Invalid message");
    }
}
function IsPresentInRoom(Obj) {
    var panelList = Obj.PanelMembers;
    var InterviewTestType = Obj.InterviewTestType;
    const peers = hmsStore.getState(selectPeers);
    var panelMembers = peers.filter(x => x.roleName == "panel");

    if (panelList.length > 0) {
        if (panelMembers.length > 0) {
            // panel members present in the room
            var p1 = panelMembers.map(x => x.name.toLowerCase());
            var p2 = panelList.map(x => x.Name.toLowerCase());
            if (p1.filter(x => p2.filter(y => y.toLowerCase() == x.toLowerCase()).length).length > 0) {
                // enable the test button
                if (InterviewTestType == "100" || InterviewTestType == "250") {
                    show(startMcqTestButton);
                    hide(startCodingTestButton);
                    return true;
                }
                else if (InterviewTestType == "200") {
                    hide(startMcqTestButton);
                    show(startCodingTestButton);
                    return true;
                }
                else {
                    show(startTestButton); 
                    hide(startMcqTestButton);
                    hide(startCodingTestButton);
                    return true;
                }
                  
            }
            else {
                //No panel member in the rooms respective to the round
                screenShareStatus.textContent = "";               
                var url = '<br><a href="' + Obj.PublicWebsite + '/User/Dashboard">Click Here for Dashboard</a>';
                $(".error-message").html("Please wait for Panel Members to Join the Room!!!" + url);
                $('.proctor-loader').css("visibility", "hidden");
                change_tab('error-message');
            }
        }
        else {
            screenShareStatus.textContent = "";            
            var url = '<br><a href="' + Obj.PublicWebsite + '/User/Dashboard">Click Here for Dashboard</a>';
            $(".error-message").html("Please wait for Panel Members to Join the Room!!!" + url);
            $('.proctor-loader').css("visibility", "hidden");
            change_tab('error-message');
            //No panel member in the rooms
        }
    }
    else {
        if (InterviewTestType == "100" || InterviewTestType == "250") {
            show(startMcqTestButton);
            hide(startCodingTestButton);
            return true;
        }
        else if (InterviewTestType == "200") {
            hide(startMcqTestButton);
            show(startCodingTestButton);
            return true;
        }      
    }
    return false;
}

function IsHTML(data) {
    return /<(?=.*? .*?\/ ?>|br|hr|input|!--|wbr)[a-z]+.*?>|<([a-z]+).*?<\/\1>/i.test(data);
}
function AfterSubmitTest(response) {
    var callBack = (RoomLeave) => {
        var apiResponse;        
        try {
            apiResponse = response;
            if (IsHTML(apiResponse)) {
                //html           
                $('.conference.new-interview-conference').empty();
                $('.conference.new-interview-conference').html(apiResponse);
            }
            else {      
                //json
                UpdateGlobalVariable(apiResponse);// updating the Global Variable
                 
                $('#load-test').val("Start MCQ Quiz");
                $('#load-coding-test').val("Start Coding Quiz");
                hide(startMcqTestButton);
                hide(startCodingTestButton);
                if (GlobalObj.ErrorMessage != null && GlobalObj.ErrorMessage != "") {
                    var url = '<br><a href="' + GlobalObj.PublicWebsite + '/User/Dashboard">Click Here for Dashboard</a>';
                    $(".error-message").html(GlobalObj.ErrorMessage + url);
                    $('.proctor-loader').css("visibility", "hidden");
                    change_tab('error-message');
                    RoomLeave();
                    return;
                }
                totalMcqTime = GlobalObj.SlotDuration;
                $('.proctor-loader').css("visibility", "hidden");
                $('#new-inject-test').html(""); 
                $(".screen-share-status").html('<div class="col-sm-4"><h4>Brainmeasures Test Platform</h4></div><div class="col-sm-5 text-right"><h5 class="screen-share-status-text"></h5></div>');
                show(presenterController);
                NextScreenShare();
            }
          
        } catch (e) {
           
        }       
    }
    handleLeave(callBack);
}

function BeforeSubmitTest() { pauseTest(); }
window.AfterSubmitTest = AfterSubmitTest;
window.BeforeSubmitTest = BeforeSubmitTest;

function InitiatingTimer() {
    //for timer
    var timer;
    var timerExamInterval;
    if (window.pauseAll) {
        window.pauseAll = false;
    }
    if (document.getElementById("exam-timer") != null) {
        var minutes, seconds;
        if (initialTimer && !window.pauseAll) {
            var duration = 60 * totalMcqTime;
            timer = duration, minutes, seconds;
            initialTimer = false;
        }
        timerExamInterval = setInterval(() => {
            countExamTimer();
            if (isNaN(timer)) {
                clearInterval(timerExamInterval);
            }
        }, 1000);
        function countExamTimer() {
            if (!window.pauseAll && !isNaN(timer)) {
                minutes = parseInt(timer / 60, 10);
                seconds = parseInt(timer % 60, 10);
                minutes = minutes < 10 ? "0" + minutes : minutes;
                seconds = seconds < 10 ? "0" + seconds : seconds;
                if (document.getElementById("exam-timer") != null) {
                    document.getElementById("exam-timer").innerHTML = maxMcqTime + " / " + minutes + ":" + seconds;
                }
                if (--timer < 0) {
                    timer = duration;
                    TestSubmitAutomatically();
                }
            }
        }
    }
}
function TestSubmitAutomatically() {
    if (window.SubmitTestAutomatically != null) {        
        window.BeforeSubmitTest = function () { pauseTest(); };
        window.SubmitTestAutomatically(AfterSubmitTest);
    }
    else {
        function callRedirect() {
            window.location.href = GlobalObj.PublicWebsite + "/User/Dashboard";
        }
        handleLeave();
        window.setTimeout(callRedirect, 3000);
    }
}

var RecordingIconDisplay = function (isRecording) {
    const localPeerId = hmsStore.getState(selectLocalPeerID);//candidate peer
    if (localPeerId) {
        const local_metadata = hmsStore.getState(selectPeerMetadata(localPeerId));
        const newLocalMetadata = { ...local_metadata, IsRecording: isRecording };
        var obj = {};
        obj[localPeerId] = newLocalMetadata;
        hmsActions.changeMetadata(obj);

        const oldLocalMetadata = { ...local_metadata };
        // update the old Metadata
        hmsActions.changeMetadata(oldLocalMetadata);
    }
}


function pauseTest() {
    window.pauseAll = true;
    if (!$(".test-question-grid").hasClass("test-pause")) {
        $(".test-question-grid").addClass("test-pause");
    }
    if (!$(".test-question-container").hasClass("test-pause")) {
        $(".test-question-container").addClass("test-pause");
    }
}

function resumeTest(message = "Resume the test by Panel") {
    ToastMessage(message, true);
    window.pauseAll = false;
    var items = document.querySelectorAll(".test-pause");
    for (var i = 0; i < items.length; i++) {
        items[0].classList.remove("test-pause");
    }
    if ($(".test-question-grid").hasClass("test-pause")) {
        $(".test-question-grid").removeClass("test-pause");
    }
    if ($(".test-question-container").hasClass("test-pause")) {
        $(".test-question-container").removeClass("test-pause");
    }
}
function UserAlertTrigger() {
    const localPeerId = hmsStore.getState(selectLocalPeerID);//candidate peer
    if (localPeerId) {

        const local_metadata = hmsStore.getState(selectPeerMetadata(localPeerId));
        const newLocalMetadata = { ...local_metadata };
        newLocalMetadata["UserAlertActivity"] = true;
        var obj = {};
        obj[localPeerId] = newLocalMetadata;
        hmsActions.changeMetadata(obj);

        const oldLocalMetadata = { ...local_metadata };
        oldLocalMetadata["UserAlertActivity"] = false;
        // update the old Metadata
        hmsActions.changeMetadata(oldLocalMetadata);
    }
}
