///* Get into full screen */
export function GoInFullscreen(element) {
	if (element.requestFullscreen)
		element.requestFullscreen();
	else if (element.oneRequestFullscreen)
		element.oneRequestFullscreen();
	else if (element.twoRequestFullscreen)
		element.twoRequestFullscreen();

	else if (element.mozRequestFullScreen)
		element.mozRequestFullScreen();
	else if (element.webkitRequestFullscreen)
		element.webkitRequestFullscreen();
	else if (element.msRequestFullscreen)
		element.msRequestFullscreen();
}


///* Is currently in full screen or not */
export function IsFullScreenCurrently(_document) {
	var full_screen_element = _document.fullscreenElement || _document.webkitFullscreenElement || _document.mozFullScreenElement || _document.msFullscreenElement || null;

	// If no element is in full-screen
	if (full_screen_element === null)
		return false;
	else
		return true;
}

export function NotificationCall(hmsNotifications, HMSNotificationTypes, CallBack, DisplayAlert = true) {
	canDisplayAlert = DisplayAlert;
	const unsubscribe = hmsNotifications.onNotification((notification) => {
		// you can use the following to show appropriate toast notifications for eg.

		switch (notification.type) {
			case 'DEVICE_CHANGE_UPDATE':
				CallBack(notification);
				break;
			case HMSNotificationTypes.PEER_LIST:
				ToastMessage(`${notification.data} are the peers in the room`); // received right after join
				break;
			case HMSNotificationTypes.PEER_JOINED:
				ToastMessage(`${notification.data.name} joined`);
				break;
			case HMSNotificationTypes.PEER_LEFT:
				ToastMessage(`${notification.data.name} left`);
				break;
			case HMSNotificationTypes.NEW_MESSAGE:
				ToastMessage(`${notification.data.message} received from ${notification.data.senderName}`);
				break;
			case HMSNotificationTypes.ERROR:
				GetErrorMessageByCode(notification.data);				
				break;
			case HMSNotificationTypes.RECONNECTING:
				ToastMessage('[Reconnecting]', notification.data);
				break;
			case HMSNotificationTypes.RECONNECTED:
				ToastMessage('[Reconnected]');
				break;
			case HMSNotificationTypes.NAME_UPDATED:
			case HMSNotificationTypes.ROLE_UPDATED:
				ToastMessage(`peer updated(${notification.type}), new peer=`, notification.data);

				break;
			case HMSNotificationTypes.METADATA_UPDATED:
				CallBack(notification);
				break;
			case HMSNotificationTypes.TRACK_DEGRADED:
				ToastMessage(`track - ${notification.data} degraded due to poor network`);
				break;
			case HMSNotificationTypes.TRACK_RESTORED:
				ToastMessage(`track - ${notification.data} recovered`);
				break;
			case HMSNotificationTypes.ROOM_ENDED:
				ToastMessage(`room ended, reason - ${notification.data.reason}`);
				break;
			case HMSNotificationTypes.REMOVED_FROM_ROOM:
				ToastMessage(`removed from room, reason - ${notification.data.reason}`);
				break;
			case HMSNotificationTypes.DEVICE_CHANGE_UPDATE:
				ToastMessage(`device changed - ${notification.data}`);
				break;
			case HMSNotificationTypes.TRACK_ADDED:
				ToastMessage(`Track added - ${notification.data}`);
				CallBack(notification);
				break;
			case HMSNotificationTypes.TRACK_REMOVED:
				ToastMessage(`Track removed - ${notification.data}`);
				CallBack(notification);
				break;
			case HMSNotificationTypes.TRACK_MUTED:
				ToastMessage(`Track removed - ${notification.data}`);
				CallBack(notification);
				break; 
			default:
				break;
		}
	});
}
export async function ScreenSharedType(data, prefix) {
	switch (data.displaySurface) {
		case "monitor":
		case "entire":
			PushTracking(prefix + 1);
			break;
		case "window":
		case "appl":
			PushTracking(prefix + 2);
			break;
		case "browser":
		case "tab":
			PushTracking(prefix + 3);
			break;
	}
}
